@import "../../global.scss";

.topbar
{
    width: 100%;
    height: 70px;
    background-color: white;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper
    {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .left
    {
        display: flex;
        align-items: center;

        .itemContainer
        {
            display: flex;
            align-items: center;
            margin-left: 50px;
            overflow: hidden;

            .home
            {
                transform: scale(1);
                font-style: 18px;
                margin-right: 5px;
                cursor: pointer;
                &:hover
                {
                    transform: scale(1.3);
                    color: red;
                }
            }

            .icon
            {
                font-style: 18px;
                margin-right: 5px;
            }

            span
            {
                font-style: 15px;
                font-weight: 500;
            }
        }
    }
    .right
    {
        .hamburger
        {
            overflow-y: hidden;
            width: 32px;
            height: 25px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            cursor: pointer;

            span
            {
                width: 100%;
                height: 3px;
                background-color: $mainColor;
                transform-origin: left;
                transition: all 1s ease;
            }
        } 
    }
    &.active
    {
        background-color: $mainColor;
        color: white;

        .hamburger
        {
            overflow-y: hidden;
            span
            {
                &:first-child
                {
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2)
                {
                    opacity: 0;
                }
                &:last-child
                {
                    background-color: white;
                    transform: rotate(315deg);
                }
            }
        }
    }
}