.intro
{
    background-color: white;
    display: flex;

    .left
    {
        flex: 0.5;
        overflow: hidden;
        .imageContainer
        {
            width: 1000px;
            height: 1000px;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;

            img
            {
                height: 100%;
                width: 100%;
            }
        }
    }

    .right
    {
        overflow: hidden;
        flex: 0.5;
        position: relative;   
        .wrapper
        {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            .name
            {
                h1
                {
                    overflow: hidden;
                    height: 80%;
                    width: 67%;
                    font-size: 130px;
                    margin: 10px 0px; 
                    box-sizing: border-box;
                    background-color:yellow;
                }
            }

            h2
            {
                font-size: 90px;
            }

            h3
            {
                font-size: 50px;
                span
                {
                    font-style: inherit;
                }

                .ityped-cursor
                {
                    animation: blink 2s infinite;
                }

                @keyframes blink
                {
                    50%{
                        opacity: 1;
                    } 
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
    
        &:hover
        {
            a
            {
                position: absolute;
                bottom: 10px;
                left: 40%;

                img
                {
                    transform: rotate(90deg);
                    width: 100px;
                    animation: flashingArrow 4s infinite;
                }

                @keyframes flashingArrow
                {
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
    }
}
